/* eslint-disable */

import { Point } from './point';
import { QuadraticEquation } from './quadratic_equation';

export class LineEquation {
  //y = a*x + b
  a!: number;
  b!: number;
  tx!: number;
  ty!: number;
  constX!: number;
  constY!: number;
  constructor(a: number, b: number) {
    this.calculate(a, b);
  }
  calculate(a: number, b: number) {
    if ((a == null || a == undefined) && (b == null || b == undefined))
      throw new Error('Either a or b must be specified');
    if (a != null && b == null) {
      this.constX = a;
    }
    if (a == null && b != null) {
      this.constY = b;
    }
    // if (a == 0 && b == 0) {this.constY = 0}
    if (a == 0) {
      this.constY = b;
    }

    // if ()
    // if (a == 0 || b == 0) {throw new Error("a and b must not be zero")}
    this.a = a;
    this.b = b;
  }
  static fromTwoPoints(
    x1: number,
    y1: number,
    x2: number,
    y2: number
  ): LineEquation {
    if (x1 == x2 && y1 == y2) {
      throw new Error('Must be two different points');
    }
    if (x1 == x2) x2 += 0.0000001;
    if (y1 == y2) y2 += 0.0000001;
    // if (x1 == x2) return new LineEquation(x1, null);
    // if (y1 == y2) return new LineEquation(null, y1);

    const b = (y2 * x1 - y1 * x2) / (x1 - x2);
    const a = x2 == 0 ? (y1 - b) / x1 : (y2 - b) / x2;

    return new LineEquation(a, b);
  }

  static fromTwoPointObjects(point1: Point, point2: Point): LineEquation {
    return LineEquation.fromTwoPoints(point1.x, point1.y, point2.x, point2.y);
  }

  getX(y: number): number {
    /* @ts-ignore */
    if (this.constY && y != this.constY) return undefined;
    /* @ts-ignore */
    if (this.constY) return null;
    if (this.constX) return this.constX;
    return (y - this.b) / this.a;
  }

  getY(x: number): number {
    /* @ts-ignore */
    if (this.constX && x != this.constX) return undefined;
    /* @ts-ignore */
    if (this.constX) return null;
    if (this.constY) return this.constY;
    return this.a * x + this.b;
  }

  clone(): LineEquation {
    return new LineEquation(this.a, this.b);
  }
  translateY(deltaY: number): LineEquation {
    if (this.constX) return this;
    if (this.constY) {
      this.constY += deltaY;
    } else {
      this.b += deltaY;
    }
    return this;
  }
  //a*x+b=a'*(x+delta)+b'

  translateX(deltaX: number): LineEquation {
    if (this.constY) return this;
    if (this.constX) {
      this.constX += deltaX;
    } else {
      this.b -= this.a * deltaX;
    }
    return this;
  }

  //https://vietjack.com/toan-lop-9/viet-phuong-trinh-duong-thang-song-song-voi-duong-thang.jsp
  translateTo(x: number, y: number): LineEquation {
    this.b = y - this.a * x;
    return this;
  }
  getIntersection(line2: LineEquation): number[] {
    /* @ts-ignore */
    if (this.a == line2.a) return null;
    /* @ts-ignore */
    if (this.constX && line2.constX) return null;
    /* @ts-ignore */
    if (this.constY && line2.constY) return null;
    if (this.constX) return [this.constX, line2.getY(this.constX)];
    if (line2.constX) return [line2.constX, this.getY(line2.constX)];
    if (this.constY) return [line2.getX(this.constY), this.constY];
    if (line2.constY) return [this.getX(line2.constY), line2.constY];

    const x = (line2.b - this.b) / (this.a - line2.a);
    const y = this.getY(x);
    return [x, y];
  }

  getIntersectionPoint(line2: LineEquation): Point {
    const intsct = this.getIntersection(line2);
    /* @ts-ignore */
    if (intsct == null) return null;
    return { x: intsct[0], y: intsct[1] };
  }

  //get angle between two lines in radians. This is the angle that is opposite to X Axis
  getAngleWithLine(line2: LineEquation): number {
    const intersection = this.getIntersection(line2);
    const x = intersection[0],
      y = intersection[1];
    const x1 = this.getX(0);
    const x2 = line2.getX(0);
    return (
      Math.atan(Math.abs(x - x1) / Math.abs(y)) +
      Math.atan(Math.abs(x - x2) / Math.abs(y))
    );
  }

  //http://toan.hoctainha.vn/Thu-Vien/Ly-Thuyet/113425/goc-giua-hai-duong-thang
  //goc nho nhat giua hai duong thang
  getAngleWithLineV2(line2: LineEquation): number {
    if (this.constX) {
      if (line2.constX) return 0;
      if (line2.constY) return Math.PI / 2;
      const y1 = 0,
        x1 = line2.getX(y1);
      const y2 = 1,
        x2 = line2.getX(y2);
      return Math.atan(y2 / (x2 - x1));
    }
    if (this.constY) {
      if (line2.constY) return 0;
      if (line2.constX) return Math.PI / 2;
      const y1 = 0,
        x1 = line2.getX(y1);
      const y2 = 1,
        x2 = line2.getX(y2);
      return Math.PI / 2 - Math.atan(y2 / (x2 - x1));
    }

    const a1 = -this.a,
      b1 = 1,
      c1 = -this.b;
    const a2 = -line2.a,
      b2 = 1,
      c2 = -line2.b;

    return Math.acos(
      Math.abs(a1 * a2 + b1 * b2) /
        (Math.sqrt(Math.pow(a1, 2) + Math.pow(b1, 2)) *
          Math.sqrt(Math.pow(a2, 2) + Math.pow(b2, 2)))
    );
  }
  //rotate clockwise at (x,y) by angle degree
  rotate(x: number, y: number, angle: number, clockwise = true): LineEquation {
    if (angle === 0) return this;

    const calAngle = clockwise ? angle : -angle;
    const alpha = (calAngle * Math.PI) / 180;
    //https://hoctoan24h.net/tim-anh-cua-mot-diem-qua-phep-quay/
    // let x1 = 0, y1 = this.getY(x1);
    // let newX1 = (x1-x)*Math.cos(alpha) - (y1-y)*Math.sin(alpha)+ x;
    // let newY1 = (x1-x)*Math.sin(alpha) + (y1-y)*Math.cos(alpha)+ y;
    const y2 = 0,
      x2 = this.getX(y2);
    const newX2 = (x2 - x) * Math.cos(alpha) - (y2 - y) * Math.sin(alpha) + x;
    const newY2 = (x2 - x) * Math.sin(alpha) + (y2 - y) * Math.cos(alpha) + y;

    const newLine = LineEquation.fromTwoPoints(x, y, newX2, newY2);
    this.calculate(newLine.a, newLine.b);
    return this;
  }

  rotateOLD(
    x: number,
    y: number,
    angle: number,
    clockwise = true
  ): LineEquation {
    if (angle === 0) return this;

    const calAngle = clockwise ? 360 - angle : angle; //dung goc ke de tinh

    const x1 = this.getX(0); //cat truc hoanh
    const y2 = this.getY(0); //cat truc tung
    const y2Angle = (Math.atan(Math.abs(x) / Math.abs(y - y2)) * 180) / Math.PI; //angle at y2 in degree
    const newY2Angle = y > y2 ? 180 - calAngle - y2Angle : calAngle + y2Angle;
    const newY2 = Math.abs(x) / Math.tan((newY2Angle * Math.PI) / 180) + y;
    const newLine = LineEquation.fromTwoPoints(x, y, 0, newY2);
    this.calculate(newLine.a, newLine.b);
    return this;
  }

  distanceFromPoint(point: Point): number {
    //https://vietjack.com/toan-lop-10/tinh-khoang-cach-tu-mot-diem-den-mot-duong-thang.jsp
    /*
        y = ax + b
        ax - y + b = 0
        => a1 = a; b1 = -1; c1 = b
        */
    const a = this.a,
      b = -1,
      c = this.b;
    return Math.abs(
      (a * point.x + b * point.y + c) /
        Math.sqrt(Math.pow(a, 2) + Math.pow(b, 2))
    );
  }

  pointsFromDistance(point: Point, distance: number): Point[] {
    const a = 1 + Math.pow(this.a, 2);
    const b = 2 * this.a * (this.b - point.y) - 2 * point.x;
    const c =
      Math.pow(point.x, 2) +
      Math.pow(this.b - point.y, 2) -
      Math.pow(distance, 2);
    // console.log("a,b,c",a,b,c)
    const quadraticEquation = new QuadraticEquation(a, b, c);
    const xs = quadraticEquation.solve();
    return [
      { x: xs[0], y: this.getY(xs[0]) },
      { x: xs[1], y: this.getY(xs[1]) },
    ];
  }
  static getHorizontalLine(x: number, y: number): LineEquation {
    return LineEquation.fromTwoPoints(x, y, x + 1000000, y + 0.000001);
  }
  static getVerticalLine(x: number, y: number): LineEquation {
    return LineEquation.fromTwoPoints(x, y, x + 0.000001, y + 1000000);
  }
}
