/* eslint-disable */
import { LineEquation } from './line_equation';
import { QuadraticEquation } from './quadratic_equation';

export class CircleEquation {
  //(x-a)^2 + (y-b)^2 = R^2
  //or: x^2 + y^2 - 2ax - 2by + (a^2 + b^2 - R^2) = 0
  constructor(public a: number, public b: number, public r: number) {}

  getX(y: number): number[] {
    const quadraticEquation = new QuadraticEquation(
      1,
      -2 * this.a,
      Math.pow(y, 2) -
        2 * this.b * y +
        Math.pow(this.a, 2) +
        Math.pow(this.b, 2) -
        Math.pow(this.r, 2)
    );
    return quadraticEquation.solve();
  }

  getY(x: number): number[] {
    const quadraticEquation = new QuadraticEquation(
      1,
      -2 * this.b,
      Math.pow(x, 2) -
        2 * this.a * x +
        Math.pow(this.a, 2) +
        Math.pow(this.b, 2) -
        Math.pow(this.r, 2)
    );
    return quadraticEquation.solve();
  }

  getIntersectWithLine(line: LineEquation): { x: number; y: number }[] {
    if (line.constX) {
      const intersectY = this.getY(line.constX);

      const result: { x: number; y: number }[] = [];
      intersectY.forEach((intsY) => {
        result.push({ x: line.constX, y: intsY });
      });
      return result;
    }

    if (line.constY) {
      const intersectX = this.getX(line.constY);

      const result: { x: number; y: number }[] = [];
      intersectX.forEach((intsX) => {
        result.push({ x: intsX, y: line.constY });
      });
      return result;
    }

    const qa = 1 + Math.pow(line.a, 2);
    const qb = 2 * line.a * line.b - 2 * this.a - 2 * this.b * line.a;
    const qc =
      Math.pow(line.b, 2) -
      2 * this.b * line.b +
      Math.pow(this.a, 2) +
      Math.pow(this.b, 2) -
      Math.pow(this.r, 2);
    const quadraticEquation = new QuadraticEquation(qa, qb, qc);
    const qr = quadraticEquation.solve();

    const result: { x: number; y: number }[] = [];
    qr.forEach((x) => {
      result.push({ x: x, y: line.getY(x) });
    });

    return result;
  }

  //http://cadasa.vn/khoi-lop-10/phuong-trinh-tiep-tuyen-cua-duong-tron.aspx
  getTangentLine(x: number, y: number): LineEquation {
    //(x - a)(x0- a) + (y - b)(y0- b) = R2
    //(x0 -a)*x - a(x0 - a) +  (y0-b)y - b(y0-b)
    //
    return new LineEquation(
      (this.a - x) / (y - this.b),
      (this.b * (y - this.b) + this.a * (x - this.a) + Math.pow(this.r, 2)) /
        (y - this.b)
    );
  }

  getLeftIntersectionWithLine(line: LineEquation): { x: number; y: number } {
    const intersections = this.getIntersectWithLine(line);
    if (intersections[0].x == intersections[1].x) {
      return intersections[0].y > intersections[1].y
        ? intersections[0]
        : intersections[1];
    } else {
      return intersections[0].x < intersections[1].x
        ? intersections[0]
        : intersections[1];
    }
  }

  getRightIntersectionWithLine(line: LineEquation): { x: number; y: number } {
    const intersections = this.getIntersectWithLine(line);
    if (intersections[0].x == intersections[1].x) {
      return intersections[0].y > intersections[1].y
        ? intersections[1]
        : intersections[0];
    } else {
      return intersections[0].x < intersections[1].x
        ? intersections[1]
        : intersections[0];
    }
  }

  //https://math.stackexchange.com/questions/256100/how-can-i-find-the-points-at-which-two-circles-intersect
  getIntersectWithCircle(circle: CircleEquation): { x: number; y: number }[] {
    const la = -(this.a - circle.a) / (this.b - circle.b);
    const lb =
      -(
        Math.pow(this.r, 2) -
        Math.pow(circle.r, 2) -
        (Math.pow(this.a, 2) - Math.pow(circle.a, 2)) -
        (Math.pow(this.b, 2) - Math.pow(circle.b, 2))
      ) /
      (2 * (this.b - circle.b));
    const line = new LineEquation(la, lb);

    const qa = 1 + Math.pow(la, 2);
    const qb = -(2 * this.a - 2 * la * (lb - this.b));
    const qc =
      Math.pow(this.a, 2) + Math.pow(lb - this.b, 2) - Math.pow(this.r, 2);
    // console.log(qa,qb,qc)
    const quadraticEquation = new QuadraticEquation(qa, qb, qc);
    const qr = quadraticEquation.solve();

    const result: { x: number; y: number }[] = [];
    // console.log(qr)
    qr.forEach((x) => {
      result.push({ x: x, y: line.getY(x) });
      // result.push({x:x,y:this.getY(x)[1]});
    });

    return result;
  }
  test(circle: CircleEquation): LineEquation {
    const la = -(this.a - circle.a) / (this.b - circle.b);
    const lb =
      -(
        Math.pow(this.r, 2) -
        Math.pow(circle.r, 2) -
        (Math.pow(this.a, 2) - Math.pow(circle.a, 2)) -
        (Math.pow(this.b, 2) - Math.pow(circle.b, 2))
      ) /
      (2 * (this.b - circle.b));
    const line = new LineEquation(la, lb);
    return line;
  }
}
