export function getRingColor(ringName:string) {
    return TECH_COLOR[ringName.charAt(0).toUpperCase()+ringName.slice(1)] || '#808080';
}
export const TECH_CIRCLE_SIZE = 14;//18;

export function calculateArcLength(r: number, alpha: number): number {
    return (alpha / 360) * 2 * Math.PI * r;
}

export function calculateAngle(r: number, arcLength: number): number {
    return 360 * arcLength / (2 * Math.PI * r);
}

export function getOverlapRect(rectangle1: IRectangle, rectangle2: IRectangle): null | Rectangle {
    const intersectionX1 = Math.max(rectangle1.x, rectangle2.x);
    const intersectionX2 = Math.min(rectangle1.x + rectangle1.width, rectangle2.x + rectangle2.width);
    if (intersectionX2 < intersectionX1) {
        return null;
    }

    const intersectionY1 = Math.max(rectangle1.y, rectangle2.y);
    const intersectionY2 = Math.min(rectangle1.y + rectangle1.height, rectangle2.y + rectangle2.height);
    if (intersectionY2 < intersectionY1) {
        return null;
    }

    return new Rectangle(
        intersectionX1,
        intersectionY1,
        intersectionX2 - intersectionX1,
        intersectionY2 - intersectionY1,
    );
}
// export getRectOverlap;

export interface IRectangle {
    readonly x: number;
    readonly y: number;
    readonly width: number;
    readonly height: number;
}

export class Rectangle implements IRectangle {
    constructor(
        public readonly x: number,
        public readonly y: number,
        public readonly width: number,
        public readonly height: number,
    ) {
    }

    get area(): number {
        return this.width * this.height;
    }
}

export function getTextSize(text: string, font: FontData): { width: number, height: number } {
    const canvas: HTMLCanvasElement = document.createElement("canvas");
    const context: CanvasRenderingContext2D = canvas.getContext("2d") as CanvasRenderingContext2D;
    context.font = `${font.size}px "${font.family}"`;
    const metrics: TextMetrics = context.measureText(text) as TextMetrics;
    // let fontHeight = metrics.fontBoundingBoxAscent + metrics.fontBoundingBoxDescent;
    const leading: number = font.leading ? Number(font.leading) : 1;
    const actualHeight = Number(font.size) * leading;
    return { width: metrics.width, height: actualHeight }
}

