export function sortRects(anchorPoints: Point[], rects: Rectangle[]): void {
    for (let i = 0; i < rects.length; ++i) {
        for (let j = rects.length - 1; j >= 0; --j) {
            rects[j] = selectRect(anchorPoints[j], rects[j], rects)
            // const r = selectRect(anchorPoints[j], rects[j], rects);
            // if (r != rects[j]) rects[j] = r;
        }
    }

}

export function selectRect(anchorPoint: Point, rect: Rectangle, rects: IRectangle[]): Rectangle {
    let area1 = 0, area2 = 0;
    let overlapRect: Rectangle | null;
    rects.forEach(item => {
        if (item != rect) {
            // console.log(texts[existingRects.indexOf(rect)])
            overlapRect = getOverlapRect(rect, item);
            // if (existingRects.indexOf(rect) == 4) {
            //     console.log(texts[existingRects.indexOf(rect)])
            //     console.log(i,overlapRect)
            // }
            if (overlapRect) {
                area1 += overlapRect.area;
                // if (area1 < overlapRect.area) {
                //     area1 = overlapRect.area;
                // }
            }
        }
    })
    if (area1 == 0) {
        // texts[existingRects.indexOf(rect)] = "XXX"
        return rect;
    }
    // const rect2 = new Rectangle(anchorPoint.x - rect.width - 8, anchorPoint.y - 6, rect.width, rect.height);
    let rect2: Rectangle;
    if (rect.x + rect.width - TECH_CIRCLE_SIZE / 2 <= anchorPoint.x) {
        rect2 = new Rectangle(anchorPoint.x - TECH_CIRCLE_SIZE / 2, rect.y, rect.width, rect.height);
    } else {
        rect2 = new Rectangle(anchorPoint.x + TECH_CIRCLE_SIZE / 2 - rect.width, rect.y, rect.width, rect.height);
    }
    // const rect2 = new Rectangle(anchorPoint.x+5-rect.width, rect.y, rect.width, rect.height);
    rects.forEach(item => {
        if (item != rect) {
            overlapRect = getOverlapRect(rect2, item);
            if (overlapRect) {
                area2 += overlapRect.area;
                // if (area2 < overlapRect.area) {
                //     area2 = overlapRect.area;
                // }
            }
        }
    })
    if (area2 == 0) {
        // texts[existingRects.indexOf(rect)] = "YYY"
        return rect2;
    }
    if (area1 < area2) {
        // texts[existingRects.indexOf(rect)] = "ZZZ " + Math.round(area1) + "/"+Math.round(area2)
        return rect;
    } else {
        // texts[existingRects.indexOf(rect)] = "KKK " + Math.round(area1) + "/"+Math.round(area2)
        return rect2;
    }
}

