import { Container, Rect, Svg, Text } from "@svgdotjs/svg.js";
import { FloatLeftLayout } from "../../drawing/floatLeftLayout";
import { Point } from "../../drawing/point";
import { Technology } from "../../models/compentency";
import { MaturityTechnologies } from "../types";

export class TechListingManager {
    private techListings: TechListing[] = [];
    constructor() { }

    createTechListing(draw: Svg, loc: Point, catName: string, maturityTechnologies: MaturityTechnologies): TechListing {
        const techListing = new TechListing(draw, loc, catName, maturityTechnologies);
        this.techListings.push(techListing);
        return techListing;
    }

    highlight(techIndex: number) {
        this.techListings.forEach(tl => tl.highlight(techIndex));
    }

    unhighlight(techIndex: number) {
        this.techListings.forEach(tl => tl.unhighlight(techIndex));
    }

    getTechListing(pos:number) {
        return this.techListings[pos];
    }
    getTechListingCount() {
        return this.techListings.length;
    }
}

class TechListingItem {
    private txt?: Text;
    private txtRect?: Rect;
    private clipRect?: Rect;
    constructor(private tech: Technology) {

    }

    highlight() {
        // this.txt?.unclip();
        this.txt?.forward();
        this.txtRect?.fill('#000000');
        this.txt?.fill('#ffffff')
    }
    unhighlight() {
        // this.txt?.clipWith(this.clipRect!);
        this.txt?.backward();
        this.txtRect?.fill('none');
        this.txt?.fill('#000000')
    }
    render(container: Container, loc: Point, offsetX: number, offsetY: number) {
        this.txt = container.text(`${this.tech.index}. ${this.tech.name}`).font({ size: 10 }).move(loc.x + offsetX, loc.y + offsetY);
        this.txt.attr('style', 'cursor:hand;');
        const bb = this.txt.bbox();
        this.clipRect = container.rect(100, bb.height).fill('none').move(bb.x, bb.y).back();
        // this.txt.clipWith(this.clipRect)

        this.txtRect = container.rect(bb.width, bb.height).fill('none').move(bb.x, bb.y).back();

        this.txt.on('mouseover', () => {
            this.highlight();
            this.tech.techCircle?.showTooltip();
        })
        this.txt.on('mouseout', () => {
            this.unhighlight();
            this.tech.techCircle?.hideTooltip();
        })
    }
}

export class TechListing {
    private techListingItems: { [key: number]: TechListingItem } = {};
    x?:number;
    y?:number;
    width?:number;
    height?:number;
    container:Container;
    constructor(draw: Svg, private loc: Point, private catName: string, private maturityTechnologies: MaturityTechnologies) {
        this.container = draw.group();
    }

    highlight(techIndex: number) {
        this.techListingItems[techIndex]?.highlight();
    }

    unhighlight(techIndex: number) {
        this.techListingItems[techIndex]?.unhighlight();
    }

    render() {
        this.container.text(this.catName.toLocaleUpperCase()).font({ size: 16 }).attr('font-weight', 'bold').move(this.loc.x, this.loc.y);
        let offsetY = 30;
        let offsetX = 120;
        let totalHeight1 = 30, totalHeight2 = 30;
        let even = true;
        const boxes: Container[] = [];
        Object.keys(this.maturityTechnologies).forEach((mat, idx) => {
            const techs = this.maturityTechnologies[mat];
            if (techs === null || techs === undefined || techs.length === 0) {
                return;
            }
            even = offsetX === 120 ? true : false;
            offsetX = even ? 0 : 120;
            offsetY = even ? totalHeight1 : totalHeight2;

            const group = this.container.group();
            group.text(mat.toLocaleUpperCase()).font({ size: 12 }).attr('font-weight', 'bold').move(this.loc.x + offsetX, this.loc.y + offsetY);
            offsetY += 18;
            techs.forEach(tech => {
                const item = new TechListingItem(tech);
                item.render(group, this.loc, offsetX, offsetY);
                this.techListingItems[tech.index!] = item;
                offsetY += 12;
            })
            // offsetY+=10;
            if (even) {
                totalHeight1 += group.bbox().height + 10;
            } else {
                totalHeight2 += group.bbox().height + 10;
            }
            boxes.push(group);
        })
        // const layout = new SpaceOptimizerLayout(240);
        // boxes.forEach(box=>{
        //     layout.addBox(box.bbox().width, box.bbox().height);
        // })
        // const positions = layout.doLayout();
        const layout = new FloatLeftLayout(240, boxes.map((b, i) => ({width: b.bbox().width, height: b.bbox().height })));
        const positions = layout.doLayout();
        boxes.forEach((box,idx) => {
            box.move(positions[idx].x + this.loc.x, positions[idx].y + this.loc.y + 30 + idx)
        });

        const bb = this.container.bbox();
        this.x = bb.x;
        this.y = bb.y;
        this.width = bb.width;
        this.height = bb.height;
    }
}