interface LayoutItem {
  width: number;
  height: number;
  cols?: number;
  rows?: number;
}

interface Cell {
  row: number;
  col: number;
  x: number;
  y: number;
  occupied: boolean;
}
export class FloatLeftLayout {
  private CELL_SIZE = 10;
  private cells: Cell[][] = [];
  private cols: number;
  private rows: number;
  constructor(private containerWidth: number, private items: LayoutItem[]) {
    for (const item of items) {
      item.cols = Math.floor(item.width / this.CELL_SIZE);
      item.rows = Math.floor(item.height / this.CELL_SIZE) + 1;
    }
    this.cols = Math.ceil(this.containerWidth / this.CELL_SIZE);
    this.rows = 1000;
    for (let i = 0; i < this.rows; ++i) {
      for (let j = 0; j < this.cols; ++j) {
        this.cells.push([]);
        this.cells[i][j] = {row:i, col:j, x: j * this.CELL_SIZE, y: i * this.CELL_SIZE, occupied: false };
      }
    }
  }

  hasSufficientArea(item:LayoutItem, fromRow:number, fromCol:number):boolean {
    if (fromRow+item.rows! > this.rows || fromCol+item.cols! > this.cols) {
      return false;
    }
    for (let i = fromRow; i < fromRow+item.rows!; ++i) {
      for (let j = fromCol; j < fromCol+item.cols!; ++j) {
        if (this.cells[i][j].occupied) {
          return false;
        }
      }
    }
    return true;
  }

  findStartCell(item:LayoutItem):Cell|null {
    for (let i = 0; i < this.rows; ++i) {
      for (let j = 0; j < this.cols; ++j) {
        if (this.hasSufficientArea(item, i, j)) {
          return this.cells[i][j];
        }
      }
    }
    return null;
  }

  doLayout(): { x: number, y: number }[] {
    const positions: { x: number, y: number }[] = [];
    for (const item of this.items) {
      const cell = this.findStartCell(item);
      if (cell != null) {
        positions.push({x:cell.x, y:cell.y});
        for (let i = cell.row; i < cell.row + item.rows!; ++i) {
          for (let j = cell.col; j < cell.col + item.cols!; ++j) {
            this.cells[i][j].occupied = true;
          }
        }
      } else {
        positions.push({x:0, y:0});
      }
    }
    return positions;
  }
}