//see https://stackoverflow.com/questions/33454438/quadratic-equation-solver-in-javascript
export class QuadraticEquation {
  a: number;
  b: number;
  c: number;
  constructor(a: number, b: number, c: number) {
    this.a = a;
    this.b = b;
    this.c = c;
  }
  solve(): number[] {
    const result =
      (-1 * this.b + Math.sqrt(Math.pow(this.b, 2) - 4 * this.a * this.c)) /
      (2 * this.a);
    const result2 =
      (-1 * this.b - Math.sqrt(Math.pow(this.b, 2) - 4 * this.a * this.c)) /
      (2 * this.a);
    return [result, result2];
  }
}
