import { Svg, SVG } from '@svgdotjs/svg.js';
import { useEffect, useRef, useState } from 'react';
import { Point } from '../../drawing/point';
import { CircleEquation } from '../../math/circle_equation';
import { LineEquation } from '../../math/line_equation';
import { Technology } from '../../models/compentency';
import { calculateArcLength, getRingColor, Rectangle, TECH_COLOR } from '../../utils/chartUtil';
import { CircleChartProps, GroupedTechnologies, MaturityTechnologies } from "../types";
import { TechCircle } from './techCircle';
import { TechListing, TechListingManager } from './techListing';


const CircleChart = (props: CircleChartProps) => {
  const {technologies, categories, maturities, width, height } = props;
  const [parentWidth, setParentWidth] = useState(0);
  const [parentHeight, setParentHeight] = useState(0);

  const container = useRef<HTMLDivElement>(null);
  const circleStrokeColor = "#e0e6eb";
  const axisStrokeColor = "#e0e6eb";

  const techListingManager = new TechListingManager();

  const padding = 0;
  // const size = Math.min(width, height) - padding;
  // const chartWidth = window.innerWidth, chartHeight = window.innerHeight;


  var size = Math.min(width-400, height) - padding;
  var circleSize: number[] = [];

  var maturitiesCount = maturities ? Object.keys(maturities).length : 0;
  var ratio = 1 - 1 / (maturitiesCount === 0 ? 1 : maturitiesCount);
  var rescale = size / (size - size * (ratio ** maturitiesCount));
  for (let idx = maturitiesCount-1; idx >= 0; --idx) {
    circleSize[idx] = size - size * (ratio ** (idx + 1));
    circleSize[idx] *= rescale;
    if (idx === 0) circleSize[idx]*=1.3;
  }

  // var centerPoint = { x: width / 2, y: height / 2 }
  var centerPoint = { x: width / 2, y: circleSize[maturitiesCount-1] / 2 + 50}

  // let existingRects: Rectangle[] = [];




  const groupTechByCatAndMaturity = (techs: Technology[]): GroupedTechnologies => {
    const result : { [catName: string]: {[maturityName:string]:Technology[]} } = {};
    categories.forEach(cat => {
      result[cat] = groupTechByMaturity(technologies.filter(item => item.category === cat));
    })
    return result;
  }


  const groupTechByMaturity = (techs: Technology[]) => {
    const result: { [key: string]: Technology[] } = {};
    Object.keys(maturities).forEach(mat => {
      result[mat] = techs.filter(item => maturities[mat] && (maturities[mat] as string[]).includes(item.maturity || ""));
    })
    return result;
  }

  const groupedTechnologies = groupTechByCatAndMaturity([]);
  //assign index
  var cnt = 0;
  Object.keys(groupedTechnologies).forEach(cat=>{
    const matTechnologies = groupedTechnologies[cat];
    Object.keys(matTechnologies).forEach(mat=>{
      const techs = matTechnologies[mat];
      techs.forEach(t=>t.index=++cnt)
    })
  })

  const getSide = (angle: number): "LEFT" | "RIGHT" => {
    const normAngle = angle;//Math.round(angle*1000)/1000;
    return (normAngle < -90 && normAngle >= -270) || (normAngle >= 90 && normAngle < 270) ? "LEFT" : "RIGHT";
  }

  const drawTechs = (draw: Svg, techData: GroupedTechnologies) => {
    // const data: { [key: string]: Technology[] } = {};
    // categories.forEach(cat => {
    //   data[cat] = techData.filter(item => item.category === cat);
    // })

    const points: Point[] = [], rects: Rectangle[] = [], techs: Technology[] = [];
    let ret;

    for (let i = 0; i < Math.min(categories.length, 4); ++i) {
      ret = drawTechCategory(draw, i, techData[categories[i]])
      // ret.points.forEach(itm=>points.push(itm))
      points.push(...ret.points);
      // Array.prototype.push.apply(points, ret.points)
      rects.push(...ret.rects);
      techs.push(...ret.techs);
    }
    // sortRects(points, rects);
    // const texts = techs.map(item => item.name || "");
    points.forEach((rect: any, idx: number) => {
      const p = points[idx];
      const techCircle = new TechCircle(techs[idx], draw, p, width/Math.min(parentWidth,parentHeight), techListingManager);
      techCircle.render();
    })

  }

  const drawTechCategory = (draw: Svg, categoryIndex: number, techs: MaturityTechnologies): { points: Point[], rects: Rectangle[], techs: Technology[] } => {
    // const techsByMaturity = groupTechByMaturity(techs);
    const points: Point[] = [], rects: Rectangle[] = [], data: Technology[] = [];
    let ret;
    const maturitiesKeys = Object.keys(maturities);
    const maturitiesCount = maturities ? maturitiesKeys.length : 0;
    for (let j = 0; j < maturitiesCount; ++j) {
      ret = drawTechMaturity(draw, categoryIndex, j, techs[maturitiesKeys[j]]);
      // ret.points.forEach(itm=>points.push(itm))
      // Array.prototype.push.apply(points, ret.points)
      points.push(...ret.points);
      rects.push(...ret.rects);
      data.push(...ret.techs);
    }
    return { points: points, rects: rects, techs: data }
  }

  const drawTechMaturity = (draw: Svg, categoryIndex: number, maturityIndex: number, data: Technology[]): { points: Point[], rects: Rectangle[], techs: Technology[] } => {
    if (data.length === 0) return { points: [], rects: [], techs: [] };
    const points: Point[] = [];
    const catAngle = 359.999 / (categories.length == 0 ? 1 : categories.length);
    const line = LineEquation.getHorizontalLine(centerPoint.x, centerPoint.y).clone().rotate(centerPoint.x, centerPoint.y, 270, true);

    let circle1: CircleEquation, circle2: CircleEquation, circle3: CircleEquation, circle4: CircleEquation, circle5: CircleEquation;
    let n1 = 0, n2 = 0, n3 = 0, n4 = 0, n5 = 0;
    let techDistance: number;
    let arcLen1: number, arcLen2: number, arcLen3: number, arcLen4: number, arcLen5: number;
    let radius1: number, radius2: number, radius3: number, radius4: number, radius5: number;
    if (maturityIndex === 0) {
      const step = circleSize[maturityIndex] / 2 / 6;
      radius1 = step;
      radius2 = step*2;
      radius3 = step*3;
      radius4 = step*4;
      radius5 = step*5;
      arcLen1 = calculateArcLength(radius1, catAngle);
      arcLen2 = calculateArcLength(radius2, catAngle);
      arcLen3 = calculateArcLength(radius3, catAngle);
      arcLen4 = calculateArcLength(radius3, catAngle);
      arcLen5 = calculateArcLength(radius3, catAngle);
      techDistance = (arcLen1 + arcLen2 + arcLen3 + arcLen4 + arcLen5) / (data.length + 1);
      // n1 = Math.floor(arcLen1 / techDistance);
      // n3 = Math.floor(arcLen3 / techDistance);
      // n2 = data.length - n1 - n3;
      n1 = Math.floor(arcLen1 / techDistance);
      n2 = Math.floor(arcLen2 / techDistance);
      n3 = Math.floor(arcLen3 / techDistance);
      n4 = Math.floor(arcLen4 / techDistance);
      n5 = data.length - n1 - n2 - n3 - n4;

      circle1 = new CircleEquation(centerPoint.x, centerPoint.y, radius1);
      circle2 = new CircleEquation(centerPoint.x, centerPoint.y, radius2);
      circle3 = new CircleEquation(centerPoint.x, centerPoint.y, radius3);
      circle4 = new CircleEquation(centerPoint.x, centerPoint.y, radius4);
      circle5 = new CircleEquation(centerPoint.x, centerPoint.y, radius5);
    } else { //if (maturityIndex == 1) {
      if (circleSize[maturityIndex] - circleSize[maturityIndex - 1] <= 100) {
        n1 = data.length;
        circle1 = new CircleEquation(centerPoint.x, centerPoint.y, circleSize[maturityIndex - 1] / 2 + (circleSize[maturityIndex] - circleSize[maturityIndex - 1]) / 2 / 2);
      } else {
        // radious1 = circleSize[maturityIndex - 1] / 2 + (circleSize[maturityIndex] - circleSize[maturityIndex - 1]) / 2 * (1 / 4);
        // radious2 = circleSize[maturityIndex - 1] / 2 + (circleSize[maturityIndex] - circleSize[maturityIndex - 1]) / 2 * (3 / 4);
        // radious3 = circleSize[maturityIndex - 1] / 2 + (circleSize[maturityIndex] - circleSize[maturityIndex - 1]) / 2 * (1/2);
        radius1 = circleSize[maturityIndex - 1] / 2 + (circleSize[maturityIndex] - circleSize[maturityIndex - 1]) / 2 * (1 / 4);
        radius2 = circleSize[maturityIndex - 1] / 2 + (circleSize[maturityIndex] - circleSize[maturityIndex - 1]) / 2 * (2 / 4);
        radius3 = circleSize[maturityIndex - 1] / 2 + (circleSize[maturityIndex] - circleSize[maturityIndex - 1]) / 2 * (3 / 4);
        arcLen1 = calculateArcLength(radius1, catAngle);
        arcLen2 = calculateArcLength(radius2, catAngle);
        arcLen3 = calculateArcLength(radius3, catAngle);
        techDistance = (arcLen1 + arcLen2 + arcLen3) / (data.length + 1);
        n1 = Math.floor(arcLen1 / techDistance);
        n3 = data.length - n1;//Math.floor(arcLen3/techDistance);
        // n1 = Math.ceil(data.length / 3);
        n2 = 0;//data.length - n1 - n3;

        // n1 = Math.ceil(data.length / 6);
        // n3 = Math.ceil(data.length / 4);
        // n2 = data.length - n1 - n3;

        circle1 = new CircleEquation(centerPoint.x, centerPoint.y, radius1);
        circle2 = new CircleEquation(centerPoint.x, centerPoint.y, radius2);
        circle3 = new CircleEquation(centerPoint.x, centerPoint.y, radius3);
      }
    }

    if (n1 > 0) {
      for (let k = 0; k < n1; k++) {
        const angle = (k + 1) * catAngle / (n1 + 1) + categoryIndex * catAngle;
        // const angle = (k + 1) * catAngle / (data.length + 1) + categoryIndex * catAngle;
        const side = getSide(angle - 90);
        const ints = side === "RIGHT" ? circle1.getRightIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true)) : circle1.getLeftIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true));
        points.push(ints);
      }
    }
    if (n2 > 0) {
      for (let k = 0; k < n2; ++k) {
        const angle = (k + 1) * catAngle / (n2 + 1) + categoryIndex * catAngle;
        // const angle = (k + 1) * catAngle / (data.length + 1) + categoryIndex * catAngle;
        const side = getSide(angle - 90);
        const ints = side === "RIGHT" ? circle2!.getRightIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true)) : circle2!.getLeftIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true));
        points.push(ints);
      }
    }

    if (n3 > 0) {
      for (let k = 0; k < n3; ++k) {
        const angle = (k + 1) * catAngle / (n3 + 1) + categoryIndex * catAngle;
        // const angle = (k + 1) * catAngle / (data.length + 1) + categoryIndex * catAngle;
        const side = getSide(angle - 90);
        const ints = side === "RIGHT" ? circle3!.getRightIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true)) : circle3!.getLeftIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true));
        points.push(ints);
      }
    }

    if (n4 > 0) {
      for (let k = 0; k < n4; ++k) {
        const angle = (k + 1) * catAngle / (n4 + 1) + categoryIndex * catAngle;
        const side = getSide(angle - 90);
        const ints = side === "RIGHT" ? circle4!.getRightIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true)) : circle4!.getLeftIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true));
        points.push(ints);
      }
    }

    if (n5 > 0) {
      for (let k = 0; k < n5; ++k) {
        const angle = (k + 1) * catAngle / (n5 + 1) + categoryIndex * catAngle;
        const side = getSide(angle - 90);
        const ints = side === "RIGHT" ? circle5!.getRightIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true)) : circle5!.getLeftIntersectionWithLine(line.clone().rotate(centerPoint.x, centerPoint.y, angle, true));
        points.push(ints);
      }
    }

    // const texts = data.map(item => item.name || "");
    // existingRects = createTextRects(points, texts)


    // return { points: points, rects: existingRects, techs: data }
    return { points: points, rects: [], techs: data }
  }


  const [chartBottom, setChartBottom] = useState(0);

  useEffect(() => {
    const containerElm = container.current;
    let w = containerElm?.clientWidth || 0;
    let h = containerElm?.clientHeight || 0;
    if (w === 0) w = h;
    if (h === 0) h = w;
    // if (chartBottom > 0) {
    //   h = Math.min(h, chartBottom);
    // }

//    alert(`${w} x ${h}`)
    // size = Math.min(width, height) - padding;

    // maturitiesCount = maturities? Object.keys(maturities).length : 0;
    // ratio = 1 - 1 / (maturitiesCount === 0 ? 1 : maturitiesCount);
    // rescale = size / (size - size * (ratio ** maturitiesCount));
    // for (let idx = 3; idx >= 0; --idx) {
    //   circleSize[idx] = size - size * (ratio ** (idx + 1));
    //   circleSize[idx] *= rescale;
    // }
    // centerPoint = { x: w / 2, y: h / 2 }

    setParentWidth(w);
    setParentHeight(h);

  }, [])

  useEffect(() => {
    const containerElm = container.current;

    // alert(filterByCompetency)
    // const data = technologies;// (filterByCompetency && filterByCompetency.length > 0) ? technologies.filter((item: Technology) => (filterByCompetency).includes(item.competency || "")) : technologies;
    let path: string;
    if (containerElm) {
      containerElm.innerHTML = "";
      if (parentWidth > 0 && parentHeight > 0 ) {
        // const draw = SVG().addTo(containerElm).size(width, height) //.attr("viewBox",`0 0 ${1000*1000/width} ${1000*1000/height}`);//.scale(600/width,600/height);
        const draw = SVG().addTo(containerElm).size(parentWidth, parentHeight) //.attr("viewBox",`0 0 ${1000*1000/width} ${1000*1000/height}`);//.scale(600/width,600/height);
          // .attr("viewBox", `0 0 ${width * width / parentWidth} ${height * height / parentHeight}`);//.scale(600/width,600/height);
          .attr("viewBox", `0 0 ${width+10} ${height+10}`);//.scale(600/width,600/height);
        // .attr("viewBox",`0 0 ${width*Math.max(width,parentWidth)/Math.min(width,parentWidth)} ${height*Math.max(height,parentHeight)/Math.min(height,parentHeight)}`);//.scale(600/width,600/height);
        const maturitiesKeys = Object.keys(maturities);
        maturitiesKeys.forEach((mat: string, maturityIndex: number) => {
          draw.group().circle(circleSize[maturityIndex]).move(centerPoint.x - circleSize[maturityIndex] / 2, centerPoint.y - circleSize[maturityIndex] / 2).fill("none").stroke({ width: maturityIndex === maturitiesKeys.length - 1 ? 10 : 2, color: circleStrokeColor });
          
          path = `M${centerPoint.x - circleSize[maturityIndex] / 2 + 25} ${centerPoint.y} A${circleSize[maturityIndex] / 2 - 25} ${circleSize[maturityIndex] / 2 - 25} 0 0 1 ${centerPoint.x + circleSize[maturityIndex] / 2 - 25} ${centerPoint.y}`
          draw.group().textPath(maturitiesKeys[maturityIndex].toUpperCase(), path).attr("text-anchor", "middle")
          .attr("startOffset", "50%")
          .fill(getRingColor(maturitiesKeys[maturityIndex]) + '88')
          .font({size:30}).attr('font-weight','bold').front()
        })
        //draw axis
        const catAngle = 359.999 / (categories.length === 0 ? 1 : categories.length);
        const catBaseLine = LineEquation.getHorizontalLine(centerPoint.x, centerPoint.y).clone().rotate(centerPoint.x, centerPoint.y, 270, true);
        let catRotateAngle: number;
        let catLineEndPoint: Point;
        const catOuterCircle = new CircleEquation(centerPoint.x, centerPoint.y, circleSize[circleSize.length - 1] / 2 + 20);
        let catRotateLine: LineEquation;
        let side: "LEFT" | "RIGHT";

        let nextCatRotateAngle: number;
        const lastCircleSize = circleSize[circleSize.length - 1];
        let catTextEndPoint: Point;
        categories.forEach((cat: string, idx: number) => {
          catRotateAngle = idx === 0 ? 0.00001 : idx * catAngle;
          catRotateLine = catBaseLine.clone().rotate(centerPoint.x, centerPoint.y, catRotateAngle, true);
          side = getSide(catRotateAngle - 90);
          catLineEndPoint = side === "RIGHT" ? catOuterCircle.getRightIntersectionWithLine(catRotateLine) : catOuterCircle.getLeftIntersectionWithLine(catRotateLine)

          draw.group().line(centerPoint.x, centerPoint.y, catLineEndPoint.x, catLineEndPoint.y).stroke({ width: 1, color: axisStrokeColor }).attr("data-SDPPTX-stroke-width", "4")
          // draw.group().path(`M${centerPoint.x} ${centerPoint.y} L${catLineEndPoint.x} ${catLineEndPoint.y}`).stroke({ width: 1, color: axisStrokeColor }).attr("data-SDPPTX-stroke-width","4")

          nextCatRotateAngle = catRotateAngle + catAngle;
          /*
          catRotateLine = catBaseLine.clone().rotate(centerPoint.x, centerPoint.y, nextCatRotateAngle, true);
          side = getSide(nextCatRotateAngle - 90);
          catTextEndPoint = side === "RIGHT" ? catOuterCircle.getRightIntersectionWithLine(catRotateLine) : catOuterCircle.getLeftIntersectionWithLine(catRotateLine)
          const meanAngle = catRotateAngle + (nextCatRotateAngle - catRotateAngle) / 2 - 90
          const rotateText = meanAngle > 0 && meanAngle < 180;
          if (rotateText) {
            path = `M${catTextEndPoint.x} ${catTextEndPoint.y} A${lastCircleSize / 2} ${lastCircleSize / 2} 0 0 0 ${catLineEndPoint.x} ${catLineEndPoint.y}`
            draw.group().textPath(categories[idx], path).attr("text-anchor", "middle").attr("dominant-baseline", "bottom").attr("startOffset", "50%");
          } else {
            path = `M${catLineEndPoint.x} ${catLineEndPoint.y} A${lastCircleSize / 2} ${lastCircleSize / 2} 0 0 1 ${catTextEndPoint.x} ${catTextEndPoint.y}`
            draw.group().textPath(categories[idx], path).attr("text-anchor", "middle").attr("startOffset", "50%");
          }
          */
          let techListingPos:Point;
          // var maturitiesCount = Object.keys(maturities).length;
          if (idx === 0) {
            techListingPos = {x:centerPoint.x+circleSize[maturitiesCount-1]/2+20,y:0}
          } else if (idx === 1) {
            techListingPos = {x:centerPoint.x+circleSize[maturitiesCount-1]/2+20,y:centerPoint.y}
          } else if (idx === 2) {
            techListingPos = {x:0,y:centerPoint.y}
          } else {
            techListingPos = {x:0,y:0}
          }
          // draw.text(cat).font({size:20}).move(techListingPos.x,techListingPos.y)
          const techListing = techListingManager.createTechListing(draw, techListingPos, cat, groupedTechnologies[cat]);
          techListing.render();
        })

        //rearrange TechListings
        let tl:TechListing, newY:number;
        if (techListingManager.getTechListingCount() >= 2) {
          tl = techListingManager.getTechListing(0);
          newY = tl ? tl.y! + tl.height! : 0;
          let techListingToMove = techListingManager.getTechListing(1);
          techListingToMove.container.move(techListingToMove.container.bbox().x, Math.max(techListingToMove.container.bbox().y, newY+ 20));
          setChartBottom(Math.max(chartBottom,techListingToMove.container.bbox().y + techListingToMove.container.bbox().height));
        }
        if (techListingManager.getTechListingCount() >= 4) {
          tl = techListingManager.getTechListing(3);
          newY = tl ? tl.y! + tl.height! : 0;
          let techListingToMove = techListingManager.getTechListing(2);
          techListingToMove.container.move(techListingToMove.container.bbox().x, Math.max(techListingToMove.container.bbox().y, newY + 20));
          setChartBottom(Math.max(chartBottom,techListingToMove.container.bbox().y + techListingToMove.container.bbox().height));
        }
        //====

        //draw data
        drawTechs(draw, groupedTechnologies);

        var foreignObject = draw.foreignObject(width, 300)
        foreignObject.add(SVG(`<div style="text-align:left;font-size:12px">
          <h3>What is the Tech Radar?</h3>
          <p>
          The Nashtech Tech Radar is a list of technologies, complemented by an assessment result, called <em>ring assignment</em>. We use four rings with the following semantics:
          </p>
          <ul>
            <li><strong>ADOPT</strong>  Technologies we have high confidence in to serve our purpose, also in large scale. Technologies with a usage culture in our Nashtech production environment, low risk and recommended to be widely used.</li>
            <li><strong>TRIAL</strong>  Technologies that we have seen work with success in project work to solve a real problem; first serious usage experience that confirm benefits and can uncover limitations. TRIAL technologies are slightly more risky; some engineers in our organization walked this path and will share knowledge and experiences.</li>
            <li><strong>ASSESS</strong>  Technologies that are promising and have clear potential value-add for us; technologies worth to invest some research and prototyping efforts in to see if it has impact. ASSESS technologies have higher risks; they are often brand new and highly unproven in our organisation. You will find some engineers that have knowledge in the technology and promote it, you may even find teams that have started a prototyping effort.</li>
            <li><strong>HOLD</strong>  Technologies not recommended to be used for new projects. Technologies that we think are not (yet) worth to (further) invest in. HOLD technologies should not be used for new projects, but usually can be continued for existing projects.</li>
          </ul>
        </div>   
        `)).move(0, chartBottom+40);
      }


    }
  }, [technologies, parentWidth, parentHeight, chartBottom]);

  return (
    <div
      // style={{ width: 'fit-content', margin: 'auto' }}
      // style={{ width: '100%', height: '100%', margin: 'auto'}}
      style={{ width: '90%', margin: 'auto' }}
      // style={{ width: '800px', margin: 'auto'}}
      ref={container}
      id="chart-canvas"
    ></div>
  );
}

export default CircleChart;
CircleChart.defaultProps = {
  width: 1000,
  height: 1000,
  title: "Technical Capability",
  competenciesFilter: []
};
