import { Container, Svg } from "@svgdotjs/svg.js";
import { Technology } from "../../models/compentency";
import { TECH_CIRCLE_SIZE, TECH_COLOR, TECH_FONT, TOOLTIP_FONT, getTextSize } from "../../utils/chartUtil";
import { Point } from "../../drawing/point";
import { TechListingManager } from "./techListing";

export class TechCircle {
    private tooltip?:Container;
    constructor(private techData:Technology, private draw:Svg, private loc:Point, private zoom:number, private techListingManager:TechListingManager ) {
        techData.techCircle = this;
    }

    showTooltip() {
        let textSize = getTextSize(this.techData.name || "", TOOLTIP_FONT);
        let tooltipSize = { width: textSize.width + 20, height: textSize.height + 10 }

        this.tooltip = this.draw.group().scale(this.zoom,this.zoom,this.loc.x,this.loc.y - tooltipSize.height/2 - 5);
        // this.tooltip.path(`M${this.loc.x - tooltipSize.width / 2} ${this.loc.y - tooltipSize.height - 10} h${tooltipSize.width} v${tooltipSize.height}  h-${tooltipSize.width} Z`).fill('#000000aa')
        this.tooltip.path(`
        M${this.loc.x - tooltipSize.width / 2 + 6} ${this.loc.y - tooltipSize.height - 10} 
        h${tooltipSize.width - 12}
        a 6 6 0 0 1 6 6
        v${tooltipSize.height - 12}  
        a 6 6 0 0 1 -6 6
        h-${(tooltipSize.width-12)/2 - 6}
        l-6 6
        l-6 -6
        h-${(tooltipSize.width-12)/2 - 6}
        a 6 6 0 0 1 -6 -6
        v-${tooltipSize.height-12}
        a 6 6 0 0 1 6 -6
        Z
        `)
        .fill('#000000cc')

        let txt = this.tooltip.text((this.techData.name) + '').fill('#ffffff').font(TOOLTIP_FONT)
        txt.center(this.loc.x, this.loc.y - 20);
        this.techListingManager.highlight(this.techData.index || 0);
    }

    hideTooltip() {
        this.tooltip?.remove();
        this.techListingManager.unhighlight(this.techData.index || 0);
    }

    render() {
        const g = this.draw.group();
        const circle = g.circle(TECH_CIRCLE_SIZE).move(this.loc.x - TECH_CIRCLE_SIZE / 2, this.loc.y - TECH_CIRCLE_SIZE / 2) //TODO: .attr("class", `${this.techData.importance === "Strategic" ? "tech-strategic" : "tech-demand"}`)
            .fill(TECH_COLOR[this.techData.maturity || ""]);
        g.text((this.techData.index || "") + '').fill('#fff').font(TECH_FONT).center(this.loc.x, this.loc.y).attr('pointer-events', 'none');
        
        circle.on('mouseover', (e) => {
            this.showTooltip();
        })
        circle.on('mouseout', (e) => {
            this.hideTooltip();
        })
    }

}